// HTML ÏöîÏÜå Í∞ÄÏ†∏Ïò§Í∏∞
const container = document.getElementById('root');

// XMLHttpRequest Í∞ùÏ≤¥ ÏÉùÏÑ±
const ajax = new XMLHttpRequest();

// API URL Ï†ïÏùò 
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json'

// Í≥µÏú†ÎêòÎäî ÏûêÏõê
const store = {
    currentPage: 1,
}


// Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞ 
function getData(url) {
    ajax.open('GET', url, false);
    ajax.send();

    return JSON.parse(ajax.response);
}


// Îâ¥Ïä§ Î™©Î°ù ÌëúÏãú
function newsFeed() {
    // Îâ¥Ïä§ Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
    const newsFeed = getData(NEWS_URL);

    const newsList = [];

    newsList.push('<ul>');

    for (let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i++){
        newsList.push(`
            <li>
                <a href="#/show/${newsFeed[i].id}">
                    ${newsFeed[i].title} (${newsFeed[i].comments_count})
                </a>
            </li>
        `);
    }

    newsList.push('</ul>');

    // ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò UI
    newsList.push(`
        <div>
            <a href="#/page/${store.currentPage > 1 ? store.currentPage-1 : 1}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
            <a href="#/page/${store.currentPage+1}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
        </div>
        `)

    container.innerHTML = newsList.join('');
}


// Îâ¥Ïä§ ÏÉÅÏÑ∏ ÌéòÏù¥ÏßÄ 
function newsDetail() {
    // location: window Í∞ùÏ≤¥Ïùò ÏÜçÏÑ±, URLÏóê ÎåÄÌïú Ï†ïÎ≥¥ Îã¥Í≥†ÏûàÏùå
    const id = location.hash.substring(7);

    // ÏÉÅÏÑ∏ Îâ¥Ïä§ APIÏóê GET ÏöîÏ≤≠
    // JSON Îç∞Ïù¥ÌÑ∞ ÌååÏã± 
    const newsContent = getData(CONTENT_URL.replace("@id", id));

    container.innerHTML = `
        <h1>${newsContent.title}</h1>
        <div>
            <a href="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
        </div>
    `;
}


// ÎùºÏö∞ÌÑ∞: Îâ¥Ïä§ ÌîºÎìúÏôÄ Îâ¥Ïä§ ÎÇ¥Ïö© ÌôîÎ©¥ Ï§ëÍ≥Ñ
function router() {
    // Ìï¥Ïãú Í∞íÏùÑ Í∏∞Ï§ÄÏúºÎ°ú ÌôîÎ©¥ Ï†ÑÌôò
    const routePath = location.hash;

    // location.hashÏóê #Îßå Îì§Ïñ¥ÏûàÏùÑ Í≤ΩÏö∞ Îπà Í∞í Î∞òÌôò
    if (routePath === '') {
        // ÎèÑÏûÖ ÌôîÎ©¥
        newsFeed();
    } else if (routePath.indexOf('#/page/') >= 0) {
        // 1Ô∏è‚É£ Ïù¥Ï†Ñ, Ïù¥ÌõÑ ÌéòÏù¥ÏßÄ Ïù¥Îèô
        // 2Ô∏è‚É£ Îâ¥Ïä§ ÎÇ¥Ïö© -> Îâ¥Ïä§ ÌîºÎìú
        store.currentPage = Number(routePath.substring(7));
        newsFeed();
    } else {
        // Îâ¥Ïä§ ÌîºÎìú -> Îâ¥Ïä§ ÎÇ¥Ïö© 
        newsDetail();
    }
}



// Îâ¥Ïä§ Ìï≠Î™© ÌÅ¥Î¶≠ Ïãú ÏÉÅÏÑ∏ ÎÇ¥Ïö© ÌëúÏãú (hashChange Ïù¥Î≤§Ìä∏)
// Ìï¥Ïãú Í∞í: URLÏùò # Îí§Ïóê Ïò§Îäî Î∂ÄÎ∂ÑÏúºÎ°ú, ÌéòÏù¥ÏßÄ ÎÇ¥ ÌäπÏ†ï ÏúÑÏπòÎÇò Ïª®ÌÖêÏ∏†Î•º ÏãùÎ≥ÑÌïòÎäîÎç∞ ÏÇ¨Ïö©
window.addEventListener('hashchange', router);

// Ï≤´ ÏßÑÏûÖ ÌôîÎ©¥
router();